import { useRouter } from 'next/router';

import { IMAGES } from 'src/constants/resouce';

import type { ProductName } from './commons';
import { getProductName } from './commons';

interface Return {
  name: ProductName;
  readableName: string;
  logoUrl: string;
}

const useProductInfo = (): Return => {
  // TODO: import { camelizeKeys } from 'humps'; 적용 필요 parma 은 snake 다.
  const router = useRouter();
  const name = getProductName(router.query?.productName as string);
  const readableName = '메디힐';
  const logoUrl = `${IMAGES}/${name}/logo.svg`;
  return {
    name,
    readableName,
    logoUrl,
  };
};

export default useProductInfo;
