export const MEDIHEAL = 'mediheal';
export const PRODUCT_NAMES = [MEDIHEAL] as const;
export type ProductName = (typeof PRODUCT_NAMES)[number];

export const getProductName = (name: string = MEDIHEAL): ProductName => {
  return PRODUCT_NAMES.includes(name as ProductName)
    ? (name as ProductName)
    : MEDIHEAL;
};
